import React, { Component } from 'react';
import {Switch, Route, HashRouter} from 'react-router-dom'
import App from './App';
import IconViewer from './IconViewer'
import {CookiesProvider, withCookies} from 'react-cookie';
import {MuiThemeProvider} from "@material-ui/core/styles";
import Consts from "./consts";

class TopApp extends Component {
  render() {
    console.log(process.env.PUBLIC_URL);
    return (
      <MuiThemeProvider theme = { Consts.THEME }>
        <CookiesProvider>
          <HashRouter basename={process.env.PUBLIC_URL}>
            <Switch>
              {
                process.env.REACT_APP_MODE !== "production" &&
                [
                  <Route path="/icon/:type" render={props => (<IconViewer {...props} cookies={this.props.cookies}/>)}/>
                  ,<Route path="/icon" render={props => (<IconViewer {...props} cookies={this.props.cookies}/>)}/>
                ]
              }
              <Route path="/:menu" render={props => (<App {...props} cookies={this.props.cookies}/>)}/>
              <Route path="/" render={props => (<App {...props} cookies={this.props.cookies}/>)}/>
            </Switch>
          </HashRouter>
        </CookiesProvider>
      </MuiThemeProvider>
    );
  }
}

export default withCookies(TopApp);
