import React, {useEffect} from 'react';
import {makeStyles} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  adRoot: props => ({
    ...props.customStyle,
  }),
}));

export default function AdSense(props) {
  const classes = useStyles(props);

  useEffect(() => {
    (window.adsbygoogle = window.adsbygoogle || []).push({});
  },[]);

  return(
    <div align="center" className={classes.adRoot}>
      {
        (window.location.hostname === "localhost" || window.location.hostname === "127.0.0.1" || window.location.hostname === "dev.smartconsumer.kr")
          ?
          props.fixed ?
            <ins className='adsbygoogle'
                 style={{display:'inline-block',width:'220px',height:'380px'}}
                 data-ad-client={props.adClient ? props.adClient : 'ca-pub-2682155550928874'}
                 data-ad-slot={props.adSlot ? props.adSlot : '4097750891'}
                 data-adtest='on'
            />
          :
            <ins className='adsbygoogle'
                 style={{display: 'block'}}
                 data-ad-client={props.adClient ? props.adClient : 'ca-pub-2682155550928874'}
                 data-ad-slot={props.adSlot ? props.adSlot : '8984188016'}
                 data-ad-format={props.adFormat ? props.adFormat : 'auto'}
                 data-ad-layout-key={props.adLayoutKey && props.adLayoutKey}
                 data-full-width-responsive={props.fullWidthResponsive ? props.fullWidthResponsive : 'true'}
                 data-adtest='on'
            />
          :
          props.fixed ?
            <ins className='adsbygoogle'
                 style={{display:'inline-block',width:'220px',height:'380px'}}
                 data-ad-client={props.adClient ? props.adClient : 'ca-pub-2682155550928874'}
                 data-ad-slot={props.adSlot ? props.adSlot : '4097750891'}
            />
          :
            <ins className='adsbygoogle'
                 style={{display: 'block'}}
                 data-ad-client={props.adClient ? props.adClient : 'ca-pub-2682155550928874'}
                 data-ad-slot={props.adSlot ? props.adSlot : '8984188016'}
                 data-ad-format={props.adFormat ? props.adFormat : 'auto'}
                 data-ad-layout-key={props.adLayoutKey && props.adLayoutKey}
                 data-full-width-responsive={props.fullWidthResponsive ? props.fullWidthResponsive : 'true'}
            />
      }
    </div>
  )
}


